:root {
  --primary-text-color: #000917;
  --title-text-color: #202025;
  --modal-text-color: #5f6775;
  --accent-color: #fd5103;
  --primary-white: #fcfcfc;
  --white: #fff;
  --background: #fcfcfc;
}

.dark {
  --primary-text-color: #fcfcfc;
  --title-text-color: #fcfcfc;
  --modal-text-color: #bdbdbd;
  --accent-color: #fd5103;
  --primary-white: #fcfcfc;
  --white: #fff;
  --background: #202025;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li {
  margin: 0;
  padding: 0;
}

body {
  letter-spacing: .02em;
  color: var(--primary-text-color);
  outline: var(--accent-color);
  background-color: var(--background);
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.list {
  border: none;
  margin: 0;
  padding: 0;
  font-style: normal;
  text-decoration: none;
  list-style: none;
}

.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 319.97px) {
  .container {
    width: 319.97px;
  }
}

@media screen and (min-width: 767.98px) {
  .container {
    width: 767.98px;
  }
}

@media screen and (min-width: 1199.97px) {
  .container {
    width: 1199.97px;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
  position: relative;
}

@media (min-width: 767.98px) {
  .header {
    justify-content: flex-start;
  }
}

@media (min-width: 767.98px) {
  .header__logo {
    margin-right: 130px;
  }
}

@media (min-width: 1199.97px) {
  .header__logo {
    margin-right: 45px;
  }
}

.header__icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-search__form {
  width: 290px;
  height: 40px;
  border: solid 1px var(--accent-color);
  border-radius: 4px;
  align-items: center;
  display: flex;
}

@media (max-width: 767.98px) {
  .header-search__form {
    display: none;
  }
}

.header-search__button {
  width: 40px;
  height: 100%;
  border: none;
  border-right: solid 1px var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
}

.header-search__icon {
  fill: var(--accent-color);
}

.header-search__input {
  color: #5f6775;
  opacity: .4;
  outline: rgba(0, 0, 0, 0);
  padding-left: 8px;
  line-height: 1.7;
}

.header-switch {
  display: none;
}

@media (min-width: 1199.97px) {
  .header-switch {
    justify-content: center;
    align-items: center;
    margin-left: auto;
    display: flex;
  }
}

.header-switch__input {
  height: 0;
  width: 0;
  visibility: hidden;
}

.header-switch__input:checked + .header-switch__label {
  background: var(--accent-color);
}

.header-switch__input:checked + .header-switch__label:after {
  background: var(--primary-white);
  left: 100%;
  transform: translateX(-100%);
}

.header-switch__label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: var(--primary-white);
  border: solid 1px var(--accent-color);
  border-radius: 100px;
  display: block;
  position: relative;
}

.header-switch__label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--accent-color);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.header-switch__label:active:after {
  width: 15px;
  right: 100%;
}

.menu {
  display: flex;
}

@media (max-width: 1199.97px) {
  .menu {
    display: none;
  }
}

.menu__list {
  margin-right: 60px;
}

.menu__list:last-child {
  margin-right: 250px;
}

.menu__link {
  color: var(--primary-text-color);
  font-size: 18px;
  line-height: 1.3;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  right: 30px;
}

@media (min-width: 1199.97px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--primary-white);
  z-index: 999;
  padding: 30px 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container__switch {
  align-items: center;
  display: flex;
}

.menu-container__input {
  height: 2px;
  width: 3px;
  visibility: hidden;
}

.menu-container__input:checked + .menu-container__label {
  background: var(--accent-color);
}

.menu-container__input:checked + .menu-container__label:after {
  background: var(--primary-white);
  left: 100%;
  transform: translateX(-100%);
}

.menu-container__label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: var(--primary-white);
  border: solid 1px var(--accent-color);
  border-radius: 100px;
  display: block;
  position: relative;
}

.menu-container__label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--accent-color);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.menu-container__label:active:after {
  width: 15px;
  right: 100%;
}

.menu-container.is-open {
  transform: translateX(0);
}

